body {
  background-color: whitesmoke;
}

.completed {
    font-style: italic;
    color: #cdcdcd;
    text-decoration: line-through
}

.todo-list {
  background-color: white;
  margin: auto;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #efefef;
  box-shadow:
  /* The top layer shadow */
      0 1px 1px rgba(0,0,0,0.15),
          /* The second layer */
      0 10px 0 -5px #eee,
          /* The second layer shadow */
      0 10px 1px -4px rgba(0,0,0,0.15),
          /* The third layer */
      0 20px 0 -10px #eee,
          /* The third layer shadow */
      0 20px 1px -9px rgba(0,0,0,0.15);
  padding: 30px;
}

.container {
  width: 90%;
  position: relative;
}

.allItems:focus { 
  background-color: #5f93e8;
}

.activeItems:focus { 
  background-color: #51cc4b;
}

.completedItems:focus { 
  background-color: #cc4b57;
}

.todo-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 30px 20px 0;
  width: 100%;
  border-bottom: 1px solid #cecece;
  font-family: Helvetica Neue;
  font-weight: 100;
  font-size: 24px;
  color: #333333;
}

.todo-input {
  width: 100%;
  font-size: 24px;
  display: inline-block;
  color: #333333;
  border: 1px solid #efefef;
  box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  padding: 5px;
  text-overflow: ellipsis;
}

.delete {
  background-color: rgb(238, 130, 130);
  border: 1px solid #efefef;
  width: 20px;
  height: 20px;
  border-radius: 25%;
  text-align: right;
  padding-left: 1px;
  position: absolute;
  right: 0;
}

input[type=checkbox] {
  margin-right: 10px;
  font-size: 30px;
}

input[type=checkbox]:focus {
  outline: 0;
}